.menu {
  box-sizing: border-box;
  height: 100vh;
  padding: $headerTopPadding 0;
  overflow-y: auto;
  background-color: $colorBlue;

  &__top {
    @include top-actions;
    margin-bottom: 48px;
  }

  &__list {
    list-style: none;
    margin: 0 0 48px;
    padding: 0;
  }
  &__item {
    margin-bottom: 24px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    color: #fff;
    text-decoration: none;

    transition: border-color 0.3s;

    &:hover {
      border-color: #fff;
    }
  }
  &__call {
    display: block;
    width: max-content;
    font-weight: 700;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: #fff;
    text-transform: uppercase;
    text-decoration: none;

    &--phone {
      margin-bottom: 16px;
    }

    &--action {
      padding-bottom: 8px;
      border-bottom: 1px solid #fff;
    }
  }
  
}
